export function rule_num_letter(e){
    var value =e.target.value
    var ascii
    var return_string=''

    for(let num=0;num<value.length;num++){
        ascii=value.codePointAt(num)
        if((ascii>=48&&ascii<=57)||(ascii>=65&&ascii<=90)||(ascii>=97&&ascii<=122)){
            return_string+=String.fromCharCode(ascii)
        }
    }
    e.target.value=return_string
    return return_string

}
export function rule_num(e,maxValue){
    var value =e.target.value
    var ascii
    var return_string=''

    for(let num=0;num<value.length;num++){
        ascii=value.codePointAt(num)
        if((ascii>=48&&ascii<=57)){
            if(num===0 && ascii===48){
                continue;//0
            }
            return_string+=String.fromCharCode(ascii)
        }
    }
    return_string=parseInt(return_string.length>0?return_string:'0')//   '0'
    return_string=return_string>maxValue?maxValue:return_string//
    e.target.value=return_string
    return return_string

}